                  >

                  </v-text-field>
                </div>
                <div class="form-group d-flex">
                  <v-text-field
                      outlined
                      class="rounded-0"
                      @keypress.enter="login()"
                      :disabled="loading"
                      v-model="pass"
                      :type="passshowO ? 'text' : 'password'"
                      label="کلمه عبور"
                      background-color="rgba(0,0,0,.05)"
                  >

                    <template  v-slot:append>
                      <v-btn v-if="!passshowO" @click="passshowO= !passshowO" icon>
                        <v-icon>mdi-eye-outline</v-icon>
                      </v-btn>
                      <v-btn v-else @click="passshowO= !passshowO" icon>
                        <v-icon>mdi-eye-off-outline</v-icon>
                      </v-btn>
                    </template>

                  </v-text-field>
                </div>
                <div class="form-group mb-5 d-flex justify-space-between">
                  <div class="w-50">
                    <a @click="status='forget'" >رمز عبور خود را فراموش کرده اید ؟</a>
                  </div>
                </div>
                <div class="form-group">
                  <v-btn :loading="loading" :disabled="loading" @click="login()" height="52"
                         class="btn btn-login rounded-0 p-3 px-5">ورود
                  </v-btn>
                </div>
              </form>
            </div>
            <div v-if="status==='forget'" class="login-wrap animate__animated animate__fadeIn p-4 pa-md-5">
              <div class="icon d-flex align-center justify-center">
                <svg xmlns="http://www.w3.org/2000/svg" width="40" fill="#fff" viewBox="0 0 448 512">
                  <path
                      d="M224 256c70.7 0 128-57.3 128-128S294.7 0 224 0S96 57.3 96 128s57.3 128 128 128zm-45.7 48C79.8 304 0 383.8 0 482.3C0 498.7 13.3 512 29.7 512H418.3c16.4 0 29.7-13.3 29.7-29.7C448 383.8 368.2 304 269.7 304H178.3z"/>
                </svg>
              </div>
              <h3 class="text-center mb-4 primary--text">فراموشی رمز عبور</h3>
              <form class="login-form">
                <div class="form-group">
                  <v-text-field
                      type="text"
                      outlined
                      autofocus
                      class="rounded-0"
                      @keypress.enter="reqForget()"
                      :disabled="loading"
                      :loading="loading"
                      @input="$v.email.$touch()"
                      @blur="$v.email.$touch()"
                      :error-messages="emailErrors"
                      v-model="email"
                      label="ایمیل"
                      placeholder="مثال : fatika@gmail.com"
                      background-color="rgba(0,0,0,.05)"
                  >

                  </v-text-field>
                </div>
                <div class="form-group mb-5 d-flex justify-space-between">
                  <div class="w-50">
                    <a @click="status='login'" >میخواهم وارد شوم</a>
                  </div>
                </div>
                <div class="form-group">
                  <v-btn :loading="loading" :disabled="loading" @click="reqForget()" height="52"
                         class="btn btn-login rounded-0 p-3 px-5">ارسال درخواست
                  </v-btn>
                </div>
              </form>
            </div>
            <div v-if="status==='resetPass'" class="login-wrap animate__animated animate__fadeIn p-4 pa-md-5">
              <div class="icon d-flex align-center justify-center">
                <svg xmlns="http://www.w3.org/2000/svg" width="40" fill="#fff" viewBox="0 0 448 512">
                  <!--! Font Awesome Pro 6.2.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. -->
                  <path
                      d="M224 256c70.7 0 128-57.3 128-128S294.7 0 224 0S96 57.3 96 128s57.3 128 128 128zm-45.7 48C79.8 304 0 383.8 0 482.3C0 498.7 13.3 512 29.7 512H418.3c16.4 0 29.7-13.3 29.7-29.7C448 383.8 368.2 304 269.7 304H178.3z"/>
                </svg>
              </div>
              <h3 class="text-center mb-4 primary--text">تغییر رمز</h3>
              <form class="login-form">
                <div class="form-group">
                  <v-text-field
                      background-color="white"
                      label="کلمه عبور جدید"
                      height="50"
                      @keypress.enter="resetPass()"
                      :disabled="loading"
                      :loading="loading"
                      :error-messages="passErrors"
                      v-model="password"
                      @input="$v.password.$touch()"
                      @blur="$v.password.$touch()"
                      :type="passshow ? 'text' : 'password'"
                      outlined
                      color="#570654"
                      class=" rounded-0 "
                  >
                    <template  v-slot:append>
                    <v-btn v-if="!passshow" @click="passshow= !passshow" icon>
                      <v-icon>mdi-eye-outline</v-icon>
                    </v-btn>
                    <v-btn v-else @click="passshow= !passshow" icon>
                      <v-icon>mdi-eye-off-outline</v-icon>
                    </v-btn>
                    </template>
                  </v-text-field>
                  <v-text-field
                      background-color="white"
                      label="تکرار کلمه عبور"
                      height="50"
                      @keypress.enter="resetPass()"
                      v-model="repeatPassword"
                      :error-messages="samepassErrors"
                      @input="$v.repeatPassword.$touch()"
                      @blur="$v.repeatPassword.$touch()"
                      :type="passshow2 ? 'text' : 'password'"
                      :disabled="loading"
                      :loading="loading"
                      outlined
                      color="#570654"
                      class=" rounded-0 "
                  >
                    <template  v-slot:append>
                      <v-btn v-if="!passshow2" @click="passshow2= !passshow2" icon>
                        <v-icon>mdi-eye-outline</v-icon>
                      </v-btn>
                      <v-btn v-else @click="passshow2= !passshow2" icon>
                        <v-icon>mdi-eye-off-outline</v-icon>
