<script>
import { validationMixin } from 'vuelidate';
import { required,email,sameAs, minLength, } from 'vuelidate/lib/validators';

import axios from "axios";
import Vue from "vue";
import FooterApp from "@/Footer";
export default {
  created() {
    this.token = this.$route.query.k;
    if(this.token){
      this.status = 'resetPass'
      axios.post(this.$store.state.userApi + 'password_reset/validate_token/',{
        token : this.token
      }).then(()=>{
        this.status = 'resetPass'
      }).catch(()=>{
        this.status = 'login';
        this.tokenFaildMessage = true;
      })

    }

  },
  name: "loginWeb",
  components: {FooterApp},
  mixins : [validationMixin],
  computed : {
    passErrors () {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.minLength && errors.push('پسورد باید دارای حداقل 6 کاراکتر باشد')
      !this.$v.password.required && errors.push('پسورد را وارد کنید')
      return errors
    },
    samepassErrors () {
      const errors = []
      if (!this.$v.repeatPassword.$dirty) return errors
      !this.$v.repeatPassword.sameAsPassword && errors.push('تکرار پسورد همخوانی ندارد')
      return errors
    },
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('ایمیل را به شکل صحیح وارد کنید')
      !this.$v.email.required && errors.push('ایمیل را وارد کنید')
      return errors
    },
  },
  validations: {
    password: {
      required,
      minLength: minLength(6)
    },
    repeatPassword: {
      sameAsPassword: sameAs('password')
    },
    email: {
      required,
      email
    },
  },
  data() {
    return {
      email : null,
      status : 'login',
      mail: null,
      pass : null,
      password: null,
      loading: false,
      data: null,
      token : null,
      repeatPassword : null,
      passshowO: false,
      passshow: false,
      passshow2: false,
      activeMessageSent : false,
      changedPassword : false,
      tokenFaildMessage : false,
    }
  },
  methods: {
    resetPass () {
      this.$v.$touch();
      if (this.$v.password.minLength && this.$v.password.required && this.$v.repeatPassword.sameAsPassword) {
        this.loading = true;
        axios.post(this.$store.state.userApi + 'password_reset/confirm/',{
          password : this.password,
          token : this.token,
        }).then(response=>{
          if(response.data.result){
            this.changedPassword = true;
            this.status = 'login'
          }
        }).catch(error=>{
          this.errorHandle(error.response,true)
        }).finally(()=>{
          this.loading = false;
        })
      }
    },
    reqForget() {
      this.$v.$touch();
      if (this.$v.email.email && this.$v.email.required) {
        this.loading = true;
        axios.post(this.$store.state.userApi + 'password_reset/',{
          email : this.email
        }).then(response=>{
          if(response.data.result){
            this.activeMessageSent = true;
            this.status = 'login'
          }
        }).catch(error=>{
          this.errorHandle(error.response,true)
        }).finally(()=>{
          this.loading = false;
        })
      }
    },
    login() {
      this.loading = true;
      let mail = this.mail;
      axios.post(this.$store.state.userApi+'token/',{
        "email": mail,
        "password": this.pass
      }).then(response=>{
        this.data = response.data;
        this.$cookie.set("token", this.data.access, { expires: '3D' });
        this.$store.state.userId = this.data.access;
        this.$store.state.refresh = this.data.refresh;
        this.$cookie.set("refresh", this.data.refresh,{ expires: '3D' });
        Vue.nextTick(() => {
          axios.get(this.$store.state.api + 'user/',{
            headers: {
              Accept: "application/json",
              Authorization: `Bearer ${this.$store.state.userId}`,
            },
          }).then(response=>{

            let profile = {
              username : response.data.data.username,
              id : response.data.data.id,
              avatar : response.data.data.avatar,
              email : response.data.data.email,
              first_name : response.data.data.first_name,
              last_name : response.data.data.last_name,
              role : response.data.data.role.value
            }

            if(response.data.data.role) {
              let permissions = response.data.data.role.permissions;
              let cleanP = {};

              for (let item in permissions) {
                cleanP[permissions[item].value]=true;
              }
              this.$cookie.set("permissions", JSON.stringify(cleanP),{ expires: '3D' });
              this.$store.state.permissions = cleanP;
            }

            this.$cookie.set("profile", JSON.stringify(profile),{ expires: '3D' });
            this.$store.state.profile = profile;

            this.$store.state.notify.color = 'success';
            this.$store.state.notify.is = true;
            this.$store.state.notify.text = "خوش آمدی ! " + mail;

          }).catch(error=>{
            this.errorHandle(error.response,true)
          }).finally(()=>{
            this.loading = false;
            this.$router.push('/');
          })

        });

      }).catch(error=>{
        this.errorHandle(error.response,true)
      }).finally(()=>{
        this.loading = false;
      })
    }
  }
}
</script>

